import React, { useState, useEffect } from 'react'
import ipfs from '../../services/ipfs'
import { useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import Bradcrumb from '../../components/Bradcrumb'

//import Loading from '../../components/Loading'
import Clip from '../../components/Clip'

//import { web3, connectMetaMask } from '../../services/web3'
import { mintToken } from '../../services/Web3/token'

import { fetchAllArtworks } from '../../store/artwork/artworkSlice'
import { isVideo } from '../../utils'

//import './index.css'
import NoImage from '../../images/No-Image.jpg'

import {categories} from '../../constant'

const Mint = (props) => {
  const dispatch = useDispatch()
  let history = useHistory()

  const userSlice = useSelector((state) => state.user)

  const [progress, setProgress] = useState('idle')

  const [imgData, setImgData] = useState({
    fileType: 'image',
    imgData: '',
    imgUrl: NoImage,
  })

  const [formData, setFormData] = useState({
    title: '',
    description: '',
    artwork: '',
    metadata: '',
    category: 'art',
  })

  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: '',
  })

  useEffect(() => {
    if (userSlice.status == 'succeeded' && userSlice.user.username == '') {
      history.push('/profile/edit')
    }
  }, [userSlice])

  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
    setDailog({
      active: false,
      status: '',
      message: '',
    })
  }
  // const handleChangeProperty = ({ target }) => {
  //   let no = target.dataset.no
  //   let properties = formData.properties
  //   properties[no] = target.value

  //   let newProperties = []

  //   properties.forEach((property) => {
  //     if (property !== '') {
  //       newProperties.push(property)
  //     }
  //   })

  //   if (newProperties[newProperties.length - 1] !== '') {
  //     newProperties.push('')
  //   }

  //   setFormData({ ...formData, properties: newProperties })
  //   setDailog({
  //     active: false,
  //     status: '',
  //     message: '',
  //   })
  // }

  const handleImgUpload = async (e) => {
    e.stopPropagation()
    e.preventDefault()
    try {
      const file = e.target.files[0]
      const name = e.target.files[0].name
      let fileType = 'image'

      let imgUrl = await readImg(file, 'read')
      let imgData = await readImg(file, 'data')

      const imgBuffer = await Buffer.from(imgData)

      if (isVideo(name)) {
        fileType = 'video'
      }

      setImgData({
        imgUrl: imgUrl,
        imgData: imgBuffer,
        fileType: fileType,
      })
      setProgress('file')
    } catch (err) {
      console.log(err)
    }
  }

  const readImg = (file, type) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      if (type == 'data') {
        reader.readAsArrayBuffer(file)
      } else {
        reader.readAsDataURL(file)
      }
      reader.onloadend = async () => {
        resolve(reader.result)
      }
    })
  }

  const uploadArtwork = async () => {
    try {
      console.log('uploading nft img')

      if (progress === 'uploading') {
        return false
      }
      setProgress('uploading')
      setDailog({
        active: false,
        status: '',
        message: '',
      })

      let path = '/nft.jpg'
      if (imgData.fileType == 'video') {
        path = '/nft.mp4'
      }

      let ipfsImgResponse = await ipfs.add(
        {
          path: path,
          content: imgData.imgData,
        },
        { wrapWithDirectory: true },
      )

      let artworkHash = ipfsImgResponse[1].hash
      artworkHash = artworkHash + path

      setFormData({ ...formData, artwork: artworkHash })

      setDailog({
        active: true,
        status: 'success',
        message: 'uploaded artwork to ipfs successfully',
      })
      setProgress('uploaded')

      // const uploadedArtwork = await createArtwork(userSlice.accessToken, {
      //   artworkHash,
      // })
      //   console.log(uploadedArtwork)
    } catch (err) {
      console.log(err)
      setFormData({ ...formData, artwork: '' })
      setProgress('file')
      setDailog({
        active: true,
        status: 'error',
        message: err.message,
      })
    }
  }

  const handleMintNFT = async (e) => {
    e.preventDefault()
    try {
      console.log('minting')

      if (
        formData.title === '' ||
        formData.description === '' ||
        formData.artwork === ''
      ) {
        throw { message: 'Fill the Form' }
      }

      setProgress('minting')

      // let properties = formData.properties
      // properties = properties.pop()
      const metaData = JSON.stringify({
        title: formData.title,
        description: formData.description,
        artwork: formData.artwork,
        category: formData.category,
       // properties: properties,
      })

      let ipfsmetaDataResponse = await ipfs.add(Buffer.from(metaData))
      let metadataHash = ipfsmetaDataResponse[0].hash

      let mintedNFT = await mintToken(metadataHash, formData.artwork)

      // fetch the artwork again in reduers
      dispatch(fetchAllArtworks())

      setProgress('uploaded')

      history.push(
        '/creator/list/' + mintedNFT.events.Mint.returnValues.tokenId,
      )
    } catch (err) {
      setProgress('uploaded')
      setDailog({
        active: true,
        status: 'error',
        message: err.message,
      })
    }
  }

  const RenderButton = () => {
    if (progress === 'uploaded') {
      return <button className="theme-btn">Uploaded to IPFS</button>
    } else if (progress === 'uploading') {
      return <button className="theme-btn  transparent-btn">Uploading.. Please wait!</button>
    } else if (progress === 'file') {
      return (
        <button className="theme-btn" onClick={uploadArtwork}>
          Upload to IPFS
        </button>
      )
    } else {
      return (
        <input
          className="uploadFileBtn"
          name="artwork"
          id="artworkInput"
          type="file"
          onChange={handleImgUpload}
        ></input>
      )
    }
  }

  return (
    <div className="root mint">
      <div className="container">
        <div style={{ margin: '120px 0' }} className="content">
         <div className="row">
         <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                  <div style={{margin: '2% 0 4% 0'}} className="section-title text-center">
                     <h2 data-watermark="Sale">Mint your Artwork</h2>
                     <p className="subtitle"></p>
                    
                  </div>
               </div>
         </div>

          <div className="row">
            <div className="col-md-6">
              <div className="mint-sideContainer">
                <div className="artworkInput-container">
                  <label htmlFor="artworkInput">
                    {imgData.fileType == 'video' ? (
                      <Clip
                        style={{ minHeight: '100%',maxHeight: '500px'  }}
                        url={imgData.imgUrl}
                        className="video"
                      />
                    ) : (
                      <img
                        style={{ minHeight: '100%',maxHeight: '500px' }}
                        className="image"
                        src={imgData.imgUrl}
                      ></img>
                    )}
                  </label>
                </div>

                <div style={{ display: 'block' }}>{RenderButton()}</div>
              </div>
            </div>
            <div className="col-md-6">
              <div className="mint-item-form">
                {dailog.active ? (
                  <div className="dailog-containter">
                    <div className={`alert alert-${dailog.status === 'error'? 'danger': 'success'}`}>
                      {dailog.message}
                    </div>
                  </div>
                ) : (
                  <div></div>
                )}

                <div className="theme-input-box">
                  <label>What kind of item are you making?</label>
                  <ul className="list-select-item clearfix">
                    {categories && categories.map((category)=>{
                      return(
                        <li key={"cat-key-"+category} onClick={()=> setFormData({...formData,category:category })}>
                      <span style={{textTransform: 'capitalize'}} className={formData.category === category? "active": ''}>{category}</span>
                      </li>
                      )
                    })}
                    
                    {/* <li>
                      <span>Collectibles</span>
                    </li>
                    <li>
                      <span>Game Items</span>
                    </li>
                    <li>
                      <span>Music</span>
                    </li>
                    <li>
                      <span>Domains</span>
                    </li>
                    <li>
                      <span>Templates</span>
                    </li>
                    <li>
                      <span>Videos</span>
                    </li> */}
                  </ul>
                </div>

                <div className="theme-input-box hide">
                  <label>Artwork Hash</label>
                  <input
                    className="theme-input"
                    required
                    readOnly
                    name="artwork"
                    placeholder="Upload Artwork for the Hash"
                    value={formData.artwork}
                    onChange={handleChange}
                  />
                </div>

                <div className="theme-input-box">
                  <label>Title </label>
                  <input
                    className="theme-input"
                    required
                    name="title"
                    placeholder="Title"
                    value={formData.title}
                    onChange={handleChange}
                  />
                </div>

                <div className="theme-input-box">
                  <label>description</label>
                  <textarea 
                    className="theme-input"
                    required
                    name="description"
                    placeholder="Description"
                    value={formData.description}
                    onChange={handleChange} id="advance-editor" rows="4">

                    </textarea>
                </div>

                <div className="theme-fix-btn">
                  {progress === 'minting' ? (
                    <button className="theme-btn transparent-btn">MINTING.. Please wait!</button>
                  ) : (
                    <button
                      className={progress === 'uploaded' ? 'theme-btn show' : 'theme-btn hide'}
                      onClick={handleMintNFT}
                    >
                      MINT NFT
                    </button>
                  )}
                </div>
              </div>
            </div>
          </div>

          {/* <div className="row-2">
            <div className="mint-sideContainer">
              <div className="artworkInput-container">
                <label htmlFor="artworkInput">
                  {imgData.fileType == 'video' ? (
                    <Clip url={imgData.imgUrl} className="video" />
                  ) : (
                    <img className="img" src={imgData.imgUrl}></img>
                  )}
                </label>
              </div>

              <div style={{ display: 'block' }}>{RenderButton()}</div>
            </div>

            <div className="mint-sideContainer">
              {dailog.active ? (
                <div className="dailog-containter">
                  <div className={dailog.status + '-alert alert'}>
                    {dailog.message}
                  </div>
                </div>
              ) : (
                <div></div>
              )}

              <div className="form-control hide">
                <label>Artwork Hash</label>
                <input
                  required
                  readOnly
                  name="artwork"
                  value={formData.artwork}
                  onChange={handleChange}
                />
              </div>
              <div className="form-control-o">
                <label>Title</label>
                <input
                  required
                  name="title"
                  placeholder="Title"
                  value={formData.title}
                  onChange={handleChange}
                />
              </div>

              <div className="form-control-o">
                <label>description</label>
                <input
                  required
                  name="description"
                  placeholder="Description"
                  value={formData.description}
                  onChange={handleChange}
                />
              </div>

              {/* <div className="form-control-o">
                <label>properties</label>
                <input
                  required
                  name="properties"
                  placeholder="e.g- art, gaming"
                  value={formData.properties}
                  onChange={handleChange}
                />
              </div> */}
          {/* {formData.properties.map((property, i) => {
                return (
                  <div className="form-control-o">
                    <label>properties</label>
                    <input
                      required
                      name={'properties'}
                      placeholder="Properties"
                      data-no={i}
                      value={property}
                      onChange={handleChangeProperty}
                    />
                  </div>
                )
              })}

              <div className="form-control-o">
                {progress === 'minting' ? (
                  <button>MINTING.. Please wait!</button>
                ) : (
                  <button
                    className={progress === 'uploaded' ? 'show' : 'hide'}
                    onClick={handleMintNFT}
                  >
                    MINT NFT
                  </button>
                )}
              </div>
            </div>
          </div>  */}
        </div>
      </div>
    </div>
  )
}
export default Mint
