import React from 'react'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'

import Countdown from '../../components/Countdown'
import Clip from '../../components/Clip'

import { selectCreatorById } from '../../store/creator/creatorSlice'

import { IMG_BASE } from '../../constant'
import { isVideo } from '../../utils'

import EmptyAvatar from '../../images/avatar.jpg'

const ArtworkCard = (props) => {
  const {
    url,
    title,
    artwork,
    price,
    description,
    username,
    owner,
    isAuction,
    endTime,
    bidders,
    creator,
  } = props.artwork

  return (
    <div className="item-group">
      <div className="item-group-content">
        <div className="hot-bids-athore clearfix">
          <a href="">
            <img src="assets/images/image.jpg" alt="" />
          </a>
          <a href="">
            <img src="assets/images/image.jpg" alt="" />
          </a>
          <a href="">
            <img src="assets/images/image.jpg" alt="" />
          </a>
        </div>
        <div className="items-like">
          <a>
            <i className="far fa-heart"></i>
          </a>
        </div>
        <div className="item-group-avtar">
          {isVideo(artwork)?(
             <Clip url={IMG_BASE + artwork} className="video" />
          ):(
            <img  className="image" src={IMG_BASE + artwork}  alt="" />
          )
          }
        
        </div>
        <h3 className="theme-title">
          <Link  to={`/${username ? username : creator}/${url}`}>{title}</Link>
        </h3>
        <p className="theme-description">
          Highest bid <span>{price / 1e18} ETH</span>
        </p>
        <div className="item-group-btn">
          <Link className="theme-btn"  to={`/${username ? username : creator}/${url}`}>
            Place Bid
          </Link>
          <Link className="item-detail-btn"  to={`/${username ? username : creator}/${url}`}>
            <i className="fas fa-info-circle"></i>
          </Link>
        </div>
      </div>
    </div>
  )
}

export default ArtworkCard
