import React from 'react'
import {Link} from 'react-router-dom'
import { claimNft } from '../../services/Web3/token'
import Clip from '../../components/Clip'
import { isVideo } from '../../utils'
import { IMG_BASE } from '../../constant'

const BidCard = ({bid}) => {

    let currentTime = parseInt(Math.floor(Date.now() / 1000))
  
    const handleNFTClaim = async (event) => {
      event.preventDefault()
  
      try {
        const tokenId = event.target.value.toString()
        let mintedNFT = await claimNft(tokenId)
        console.log(mintedNFT)
      } catch (err) {
        console.log(err)
      }
    }

    return(
        <div className="view-store-item">
        <span className="store-label alert alert-success">I'm {bid.status}</span>
        <div className="view-store-box">
           <div className="view-store-images">
              {/* <a href=""> */}
              {isVideo(bid.artwork)?(
             <Clip url={IMG_BASE + bid.artwork} className="video" />
          ):(
            <img  className="image" src={IMG_BASE + bid.artwork}  alt="" />
          )
          }
                  
                  {/* </a> */}
           </div>
           <div className="view-store-info">
              <h3 className="user-theme-title">{bid.title}</h3>
              <p className="theme-description">{bid.description}</p>
              <ul>
                 <li>Token ID : <span>{bid.tokenId}</span></li>
                 <li>Creator : <span>{bid.username}</span></li>
              </ul>
              <div className="item-group-box clearfix">
                 <div className="item-group-bid">
                    <p className="theme-description">Purchased for:</p>
                    <h2>{bid.price / 1e18} {bid.currency}</h2>
                 </div>
                 
                 {currentTime > parseInt(bid.endTime) ? (
              <div className="item-group-bid">
              <button value={bid.tokenId} onClick={handleNFTClaim} className="theme-btn">Collect</button>
           </div>
          ) : (
            ''
          )}
          {bid.status == 'Outbid' ? (
                 <div className="item-group-bid">
            <Link className="theme-btn" to={`${bid.username}/${bid.url}`}> Bid Again</Link>
            </div>
          ) : (
            <div className="item-group-bid">
            <Link className="theme-btn" to={`${bid.username}/${bid.url}`}> View </Link>
            </div>
          )}
  
                 {/* <div className="item-group-bid">
                    <a className="theme-btn transparent-btn" href="review-seller.html">Review this seller <i className="fas fa-arrow-right"></i></a>
                 </div> */}
              </div>
              <ul>
                 {/* <li>Creator : <span>{bid.username}</span></li> */}
                 {/* <li>File : <span>P_Crypto.jpg</span></li> */}
              </ul>
           </div>
        </div>
        <p className="theme-description browse-description">This painting is complete made with oil paint showcasing a man with grey hat, you can see all the fine details cearly and this paithing one of it kind</p>
     </div>
    )
}

export default BidCard