import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector  } from 'react-redux'
import {Link} from 'react-router-dom'
import Clip from '../../components/Clip'
import Loading from '../../components/Loading'
import Countdown from '../../components/Countdown'

import { isVideo } from '../../utils'

import {
  selectArtworkById,
  fetchArtwork
} from '../../store/artwork/artworkSlice'

import { IMG_BASE, TOKEN_URI } from '../../constant'

import { ReactComponent as EtherscanIcon }  from '../../images/svg/etherscan.svg'
import { ReactComponent as ExitIcon }  from '../../images/svg/exit.svg'
import { ReactComponent as IPFSIcon }  from '../../images/svg/ipfs.svg'
import { ReactComponent as ArrowDownIcon }  from '../../images/svg/arrow-down.svg'

import ArtworkDetails from './_ArtworkDetails'
import AuctionDetails from './AuctionDetails'
import ArtworkHistory from './_ArtworkHistory'


const Artwork = (props) => {

    const dispatch = useDispatch()

    const { creator: creatorUsername, artwork: artworkId } = props.match.params
    const artwork = useSelector((state) => selectArtworkById(state, artworkId))
    const error = useSelector((state) => state.artwork.error)
    
    if (error) {
      console.log('ERROR: ' + error)
    }
  
    useEffect(()=>{
      if(!artwork){
        dispatch(fetchArtwork(artworkId))
      }
  
    },[artwork])
  
    if (!artwork) {
      return <Loading/>
    }

    return(
        <section className="browse-detail-area page-paddings">
        <div className="container">
           <div className="row">
              <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                 <div className="browse-detail-images text-center">
                    <div className="browse-detail-large">
                    {isVideo(artwork.artwork) ? (
            <Clip className="video" url={IMG_BASE + artwork.artwork} />
          ) : (
            <img className="img" src={IMG_BASE + artwork.artwork} />
          )}
                       {/* <img src="/assets/images/art_detail_7.png" alt=""/> */}

                    </div>
                    {/* <div className="browse-thumblain">
                       <ul className="clearfix">
                          <li><a href=""><img src="/assets/images/art_7.png" alt=""/></a></li>
                          <li><a href=""><img src="/assets/images/art_3.png" alt=""/></a></li>
                          <li><a href=""><img src="/assets/images/art_4.png" alt=""/></a></li>
                          <li><a href=""><img src="/assets/images/art_5.png" alt=""/></a></li>
                       </ul>
                    </div>
                    <div className="browse-vote-btn">
                       <a href="" className="theme-btn">Vote on this item <i className="fas fa-arrow-right"></i></a>
                    </div> */}
                 </div>
          
                

              </div>
              <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                 <div className="browse-detail-info">
                    <h1>{artwork.title}</h1>
                    <div className="store-ratting">
                       <i className="fas fa-star-half-alt"></i><i className="fas fa-star-half-alt"></i><i className="fas fa-star-half-alt"></i><i className="fas fa-star-half-alt"></i><i className="far fa-star"></i> (3.5)
                    </div>
                    <h3 className="theme-title">See how Sky discovered the City S-03</h3>
                    <p className="theme-description">{artwork.description}</p>
                    <div className="starting-bid">
                       <ul>
                          <li>Top seller <span><img src="https://d2alktbws33m8c.cloudfront.net/badges.svg" alt=""/></span></li>
                          <li>Token ID: <span>{artwork.tokenId}</span></li>
                          <li>Edition: <span>1/1</span></li>
                          <li>Bscscan: <span><a  href={TOKEN_URI + artwork.tokenId} target="_blank">
                                TOKEN {artwork.tokenId}
                              {/* <EtherscanIcon/> */}
                              </a></span>
                           </li>
                          <li>IPFS: <span><a  href={IMG_BASE + artwork.metadata} target="_blank">
                                {artwork.metadata}
                              {/* <IPFSIcon/> */}
                              </a></span>
                           </li>

                          {/* <li>  <a  href={TOKEN_URI + artwork.tokenId} target="_blank">
            <EtherscanIcon/>
            <div className="text">View on Bscscan</div>
            <div className="exit"><ExitIcon/></div>
          </a>

          </li>
          <li><a className="external-link"  href={IMG_BASE + artwork.metadata} target="_blank" rel="noreferrer">
            <IPFSIcon/>
            <div className="text">View on IPFS</div>
            <div className="exit"><ExitIcon/></div>
          </a></li> */}
                          {/* <li>Copyright Transferred: <span style={{color: '#008000'}}>Yes</span></li>
                          <li>Downloadable file: <span style={{color: '#008000'}}>Yes</span></li>
                          <li>Resellable: <span style={{color: '#008000'}}>Yes</span></li> */}
                       </ul>
                    </div>
                    {/* <p className="theme-description">5% royalty on secondary sales</p> */}
                  
                  
                   
                 </div>
              </div>
           </div>
       
        <div className="row">
            <div className="col-md-6">
            <div className="browse-detail-info">
            <ArtworkHistory artwork={artwork} />

                 </div>

            </div>
            <div className="col-md-6">
            <div className="browse-detail-info">
            {artwork.isAuction? (<AuctionDetails artwork={artwork} />): (<></>)}

                 </div>
            </div>
        </div>


        </div>
     </section>
    )

    
}
export default Artwork