import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Link, useHistory } from 'react-router-dom'

import Countdown from '../../components/Countdown'

import { getAuction } from '../../services/Web3/token'

import { placeBet } from '../../services/Web3/token'
import { getCurrentBalance } from '../../services/Web3/web3'
import {
  selectArtworkById,
  syncAuction,
} from '../../store/artwork/artworkSlice'
import { selectCreatorById } from '../../store/creator/creatorSlice'

import ArtworkCard from '../../components/Card/ArtworkCard'

import { IMG_BASE, LEAST_BET } from '../../constant'

import AuctionFooter from './auction/AuctionFooter'

const ETH_RATE = 562.3

const AuctionDetails = ({ artwork }) => {
  const dispatch = useDispatch()
  const history = useHistory()

  const artworkSlice = useSelector((state) => state.artwork)
  const userSlice = useSelector((state) => state.user)

  const [formData, setFormData] = useState({
    token: 0,
    value: 0,
    leastBid: 0,
  })

  const [balance, setBalance] = useState({
    ether: 0,
  })

  const [buttonState, setButtonState] = useState({
    bid: false,
  })

  const [artworkAuction, setArtworkAuction] = useState({
    active: false,
    creator: '0',
    endTime: '0',
    owner: '0',
    value: '0',
  })

  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: '',
  })

  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
    setDailog({
      active: false,
      status: '',
      message: '',
    })
  }

  useEffect(() => {
    try {
      if (artwork) {
        let price = parseFloat(artwork.price)
        price = price + (price * LEAST_BET) / 100
        price = price / 1e18
        price = parseFloat(price).toFixed(6)

        setFormData({ ...formData, leastBid: price })
      }
    } catch (err) {
      console.log(err)
    }
  }, [artwork])

  useEffect(() => {
    try {
      if (userSlice.user) {
        ;(async () => {
          let balance = await getCurrentBalance()
          balance = parseFloat(balance).toFixed(6)
          setBalance({ ...balance, ether: balance })
        })()
      }
    } catch (err) {
      console.log(err)
    }
  }, [userSlice.user])

  const handlePlaceBid = async (e) => {
    e.preventDefault()
    try {
      if (!userSlice.user) {
        return
      }
      setButtonState({ ...buttonState, bid: true })
      let value = parseFloat(formData.value)
      if (value <= 0 && value < formData.leastBid) {
        setDailog({
          active: true,
          status: 'error',
          message: 'value must be greater than the least bid',
        })
        setButtonState({ ...buttonState, bid: false })
        return
      }

      let bidedTx = await placeBet(artwork.tokenId, value)

      dispatch(syncAuction({ tokenId: artwork.tokenId }))
      // setButtonState({ ...buttonState, bid: false })
      history.push('/bids')
    } catch (err) {
      console.log(err)
      setDailog({
        active: true,
        status: 'error',
        message: 'invalid value',
      })
      setButtonState({ ...buttonState, bid: false })

      return
    }
  }

  let unmounted = false
  useEffect(() => {
    if (artwork) {
      verifyAuction(artwork)
    }
    return () => {
      unmounted = true
    }
  }, [artwork])

  const verifyAuction = async (artwork) => {
    try {
      let { active, creator, value, endTime, owner } = await getAuction(
        artwork.tokenId,
      )
      if (active) {
        if (
          value !== artwork.price ||
          endTime !== artwork.endTime ||
          owner !== artwork.owner
        ) {
          if (artworkSlice.status !== 'loading') {
            if (!unmounted) {
              dispatch(syncAuction({ tokenId: artwork.tokenId }))
            }
          }
        }
      } else {
        if (active != artwork.isAuction) {
          if (!unmounted) {
            dispatch(syncAuction({ tokenId: artwork.tokenId }))
          }
        }
      }
      if (!unmounted) {
        setArtworkAuction({
          active: active,
          creator: creator,
          value: value,
          endTime: endTime,
          owner: owner,
        })
      }
    } catch (err) {
      if (!unmounted) {
        setArtworkAuction({
          active: artwork.active,
          creator: artwork.creator,
          value: artwork.value,
          endTime: artwork.endTime,
          owner: artwork.owner,
        })
      }

      console.log(err)
    }
  }

  let price = artwork.price ? artwork.price : 0
  let endTime = parseInt(artworkAuction.endTime)
  let currentTime = parseInt(Math.floor(Date.now() / 1000))
  let dif = endTime - currentTime
  let canBuy = false
   canBuy = userSlice.user && userSlice.user.publicAddress !== artwork.creator
   if(dif <= 0 && (artwork.bidders).length > 0  ) {
    canBuy = false
  }


  if (artworkAuction && artworkAuction.active) {
    price = artworkAuction.value
  }
  price = parseFloat(price) / 1e18

  const RightDetail = (artworkAuction) => {
    if (artworkAuction && artworkAuction.active) {
      return (
        <div>
          <div className="mini-text">Auction ending in</div>
          <Countdown type="main-countdown" endTime={artworkAuction.endTime} />
        </div>
      )
    } else {
      return (
        <div>
          <div>Owned By</div>
          <span className="owner">{artwork.owner}</span>
        </div>
      )
    }
  }

  
  return (
    <div className="browse-bid-detail">
       {canBuy? (
        <div className="browse-bid-box">
          <h2>
            Price :
            <span>
              {artwork.price / 1e18} {artwork.currency}
            </span>
          </h2>
          <p className="theme-description">Min. Bid: {formData.leastBid} ETH</p>
          <div
            className="nft-input-box validate-input"
            data-validate="Name is required"
          >
            <p className="theme-description">ETH</p>
            <input
              className="nft-input"
              type="number"
              placeholder="Enter your bid"
              min="0"
              step="0.1"
              name="value"
              value={formData.value}
              onChange={handleChange}
            />
            <span className="focus-nft-input"></span>
          </div>
          <div className="browse-buy-btn text-center">
            {buttonState.bid ? (
              <button className="theme-btn transparent-btn">
                Bidding.. Please wait
              </button>
            ) : (
              <button className="theme-btn" onClick={handlePlaceBid}>
                Place Bid
              </button>
            )}
          </div>
        </div>
      ) : (
        <div className="browse-bid-box">
          <h2>
            Price :
            <span>
              {artwork.price / 1e18} {artwork.currency}
            </span>
          </h2>
        </div>
      )}

      <div className="browse-detail-timer">
        <div className="browse-time-left">
          <Countdown endTime={artwork.endTime} type="artwork-card-2" />
        </div>
        <div className="browse-time-right">
          <h2>Time is Running Out!</h2>
        </div>
      </div>
      <div className="browse-bid-footer text-center">
        <p className="theme-description">
          Bids: <span>{artwork.bidders.length}</span>
        </p>
      </div>
      {/* <div className="browse-buy-btn ">
        <Link
          to={`/${artwork.username ? artwork.username : artwork.creator}/${
            artwork.url
          }/bid`}
        ><button className="theme-btn">Buy Now</button>
        </Link>
      </div> */}
      {userSlice.user ? (<AuctionFooter artwork={artwork} artworkAuction={artworkAuction}/>):(<div className="browse-bid-btn ">
       <button className="theme-btn transparent-btn">Conenct To MetaMask</button>
      </div> )}
    </div>
  )
}

export default AuctionDetails
