import React from 'react'
import { useSelector } from 'react-redux'
import ArtworkCard from '../../components/Card/ArtworkCard'

const MostViewed = () => {
  let artworks = useSelector((state) => state.artwork.artworks)

  if(artworks.length > 8){
    artworks = artworks.slice(0,8)
  }

  return (
    <section
      className="most-viewed-items-area page-paddings"
      style={{ paddingTop: '0px' }}
    >
      <div className="container">
        <div className="row">
          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
            <div className="section-title text-center">
              <h6>Items</h6>
              <h2 data-watermark="Items">Items Viewed The Most</h2>
              <div className="em_bar">
                <div className="em_bar_bg"></div>
              </div>
              <p className="subtitle">
                You can see the best and most viewed items; from all categories,
                we have a wide range of items available on our marketplace.
              </p>
            </div>
          </div>
        </div>
        <div className="row">
          {artworks &&
            artworks.map((artwork) => {
              return (
                <div
                  key={artwork._id}
                  className="col-xl-3 col-lg-4 col-md-6 col-sm-6 col-12"
                >
                  <ArtworkCard artwork={artwork} />
                </div>
              )
            })}

          <div className="bid-shape">
            <svg viewBox="0 0 300 322">
              <defs>
                <linearGradient id="gradient-2" gradientTransform="rotate(90)">
                  <stop offset="5%" stopColor="rgba(233,244,255,0)"></stop>
                  <stop offset="95%" stopColor="rgba(233,244,255,1)"></stop>
                </linearGradient>
              </defs>
              <path
                fill="url(#gradient-2)"
                d="M300,196.58v-71.16c0-31.57-16.84-60.75-44.19-76.53l-61.63-35.58c-27.34-15.79-61.03-15.79-88.37,0 L44.19,48.89C16.84,64.67,0,93.85,0,125.42v71.16c0,31.57,16.84,60.75,44.19,76.53l61.63,35.58c27.34,15.79,61.03,15.79,88.37,0 l61.63-35.58C283.16,257.33,300,228.15,300,196.58z"
              ></path>
            </svg>
          </div>
        </div>
        <div className="row">
          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
            <div className="page-btn text-center" style={{ marginTop: '50px' }}>
              <button className="theme-btn">
                Show More <i className="fas fa-arrow-right"></i>
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default MostViewed
