import React from 'react'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'

import Countdown from '../../components/Countdown'
import Clip from '../../components/Clip'

import { selectCreatorById } from '../../store/creator/creatorSlice'

import { IMG_BASE } from '../../constant'
import { isVideo } from '../../utils'

import EmptyAvatar from '../../images/avatar.jpg'

const ArtworkCard = (props) => {
  const {
    url,
    title,
    artwork,
    price,
    description,
    username,
    owner,
    isAuction,
    endTime,
    bidders,
    creator,
  } = props.artwork

  return (
    <div className="view-store-item">
    <span className="store-label">Boosted</span>
    <div className="view-store-box">
       <div className="view-store-images">
       {isVideo(artwork)?(
             <Clip url={IMG_BASE + artwork} className="video" />
          ):(
            <img  className="image" src={IMG_BASE + artwork}  alt="" />
          )
          }
       </div>
       <div className="view-store-info">
          <h3 className="theme-title">
          <Link  to={`/${username ? username : creator}/${url}`}>{title}</Link>

              </h3>
          <div className="store-ratting">
             <i className="fas fa-star-half-alt"></i>
             <i className="fas fa-star-half-alt"></i>
             <i className="fas fa-star-half-alt"></i>
             <i className="fas fa-star-half-alt"></i>
             <i className="fas fa-star-half-alt"></i> (4.5)
          </div>
          <ul>
             <li>Creator : <span>{username}</span></li>
             {/* <li>Creator : <span>{username} (3 Items)</span></li> */}
             {/* <li>Owner : <span>{owner}</span></li> */}
          </ul>
          <div className="item-group-box clearfix">
             <div className="item-group-timer">
             <Countdown type="artwork-card-2" endTime={endTime}/>
             </div>
             <div className="item-group-bid">
                <p className="theme-description">Starting bid:</p>
                <h2>{price / 1e18} ETH</h2>
             </div>
          </div>
          <div className="item-group-btn">
          <Link className="theme-btn"  to={`/${username ? username : creator}/${url}`}>
            Place Bid
          </Link>
          </div>
       </div>
    </div>
    <p className="theme-description browse-description">
        {description}
    </p>
 </div> )
}

export default ArtworkCard
