import jwtDecode from 'jwt-decode'
const axios = require('axios').default
axios.defaults.headers.post['Content-Type'] = 'application/json';

export const fetchUser = (accessToken) => {
  return new Promise((resolve, reject) => {
    const { payload: { id } } = jwtDecode(accessToken)
    axios(`/api/users/${id}`, {
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
    })
      .then((response) => {
        resolve(response.data)
      })
      .catch((err) => {
        console.log(err)
        reject(err.message ? err.message : 'internal error')
        return
      })
  })
}

export const patchUser = async (accessToken, data) => {
  return new Promise(async (resolve, reject) => {
    try {
      let response = await axios({
        headers: {Authorization: `Bearer ${accessToken}`},
        url: `/api/users/profile/edit`,
        method: 'POST',
        data: JSON.stringify(data),
      })

      resolve(response.data)
      return
    } catch (err) {
      console.log(err)
      return reject(err.message ? err.message : 'internal error')
    }
  })
}


export const followCreator = async (accessToken, data) => {
  return new Promise(async (resolve, reject) => {
    try {
      let response = await axios({
        url: `/api/users/follow/creator`,
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        method: 'post',
        data: JSON.stringify(data),
      })

      resolve(response.data)
      return
    } catch (err) {
      console.log(err)
      return reject(err.message ? err.message : 'internal error')
    }
  })
}