import React, { useState, useEffect } from 'react'
import Web3 from 'web3'
import { useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { Container, Grid, makeStyles, Button } from '@material-ui/core'

import Loading from '../../components/Loading'

import { listToken, getAuction } from '../../services/Web3/token'

import {
  selectArtworkByTokenId,
  fetchAllArtworks,
  fetchArtwork,
  syncAuction,
} from '../../store/artwork/artworkSlice'

import Clip from '../../components/Clip'
import { isVideo } from '../../utils'

import { IMG_BASE } from '../../constant/'

const List = (props) => {
  const dispatch = useDispatch()
  let history = useHistory()

  let { tokenId } = props.match.params

  //remove
  tokenId = tokenId.toString()

  const userSlice = useSelector((state) => state.user)
  //  const artworkSlice = useSelector((state) => state.artwork)
  const artwork = useSelector((state) => selectArtworkByTokenId(state, tokenId))

  const [formData, setFormData] = useState({
    price: 0,
    currency: 'ETH',
    type: 'auction',
  })

  const [buttonState, setButtonState] = useState({
    listing: false,
  })
  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: '',
  })

  let lookingForArtworkInterval = null
  useEffect(() => {
    if (!artwork) {
      //get the awrwork from server
      dispatch(fetchArtwork(tokenId))

      lookingForArtworkInterval = setInterval(() => {
        console.log('looking for artwork')
        dispatch(fetchArtwork(tokenId))
      }, 5000)
    }
    if (artwork) {
      clearInterval(lookingForArtworkInterval)
    }

    return () => {
      clearInterval(lookingForArtworkInterval)
    }
  }, [artwork])

  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
  }

  const handleListNFT = async () => {
    try {
      console.log('listing')
      setButtonState({ ...buttonState, listing: true })

      let artworkAuction = await getAuction(tokenId)
      if (artworkAuction.active) {
        dispatch(syncAuction())
        throw 'Already Listed in Auction'
      }

      let currency = formData.currency === 'ETH' ? '0' : '1'

      let ListedNFT = await listToken(tokenId, formData.price, currency)

      dispatch(fetchAllArtworks())

      setButtonState({ ...buttonState, listing: false })
      history.push(`/${artwork.username}/${artwork.url}`)

      // console.log(savedArtwork)
    } catch (err) {
      setButtonState({ ...buttonState, listing: false })
      console.log(err)
    }
  }

  if (
    !artwork ||
    !userSlice.user ||
    artwork.creator !== userSlice.user.publicAddress
  ) {
    return <Loading />
  }

  return (
    <div className="root mint">
      <div className="container">
        <div style={{ margin: '120px 0' }} className="content">
          <div className="row">
            <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
              <div
                style={{ margin: '2% 0 4% 0' }}
                class="section-title text-center"
              >
                <h2 data-watermark="Sale">List your Artwork</h2>
                <p class="subtitle"></p>
              </div>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              {' '}
              <div className="mint-sideContainer">
                <div className="artworkInput-container">
                  <label htmlFor="artworkInput">
                    {isVideo(artwork.artwork) ? (
                      <Clip
                        style={{ minHeight: '100%', maxHeight: '500px' }}
                        url={IMG_BASE + artwork.artwork}
                        className="video"
                      />
                    ) : (
                      <img
                        style={{ minHeight: '100%', maxHeight: '500px' }}
                        className="image"
                        src={IMG_BASE + artwork.artwork}
                      ></img>
                    )}
                  </label>
                </div>
              </div>
            </div>
            <div className="col-md-6">
              <div className="mint-item-form">
                {dailog.active ? (
                  <div className="dailog-containter">
                    <div className={dailog.status + '-alert alert'}>
                      {dailog.message}
                    </div>
                  </div>
                ) : (
                  <div></div>
                )}

                <div className="theme-input-box">
                  <label>Type</label>
                  <ul className="list-select-item clearfix">
                    <li
                      onClick={() =>
                        setFormData({ ...formData, type: 'auction' })
                      }
                    >
                      <span
                        style={{ textTransform: 'capitalize' }}
                        className={formData.type === 'auction' ? 'active' : ''}
                      >
                        Auction
                      </span>
                    </li>

                    <li
                      onClick={() =>
                        setFormData({ ...formData, type: 'fixed' })
                      }
                    >
                      <span
                        style={{ textTransform: 'capitalize' }}
                        className={formData.type === 'fixed' ? 'active' : ''}
                      >
                        Fixed Price
                      </span>
                    </li>
                  </ul>
                </div>

                <div className="theme-input-box">
                  <label>Price</label>
                  <input
                    className="theme-input"
                    required
                    name="price"
                    placeholder="price"
                    value={formData.price}
                    onChange={handleChange}
                  />
                </div>

                <div className="theme-input-box">
                  <label for="currency" className="">
                    Currency
                  </label>
                  <select
                    className="theme-input"
                    required
                    type="text"
                    name="currency"
                    placeholder="currency"
                    value={formData.currency}
                    onChange={handleChange}
                  >
                    <option value="ETH">ETH</option>
                    <option value="DIGIPT">DIGIPT</option>
                  </select>
                </div>

                {/* <div className="theme-input-box">
                  <label for="type" className="">
                    Type
                  </label>
                  <select
                    required
                    type="text"
                    name="type"
                    placeholder="type"
                    value={formData.type}
                    onChange={handleChange}
                  >
                    <option value="Auction">Auction</option>
                    <option value="Fixed">Fixed Price</option>
                  </select>
                </div> */}

                <div className="theme-input-box">
                  {buttonState.listing ? (
                    <button className="theme-btn  transparent-btn">
                      Listing.. Please wait!
                    </button>
                  ) : (
                    <button className="theme-btn" onClick={handleListNFT}>
                      List your NFT
                    </button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default List
