import React, { useEffect, useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import Loading from '../../components/Loading'
import ArtworkCard2 from '../../components/Card/ArtworkCard2'
import AvatarImg from '../../images/avatar.jpg'
import CoverImg from '../../images/cover.jpg'
import { selectCreatorById, updateCreators} from '../../store/creator/creatorSlice'

import Clip from '../../components/Clip'
import { isVideo } from '../../utils'
import { creatorAPI } from '../../api/creator'
import { artworkAPI } from '../../api/artwork'

import { IMG_BASE } from '../../constant'
import Sidebar from './Sidebar'
import './index.css'

const Creator = (props) => {
  const dispatch = useDispatch()
  const history = useHistory()

  const { creator: creatorUsername } = props.match.params

  const creator = useSelector((state) =>
    selectCreatorById(state, creatorUsername),
  )
  //console.log(selectedCreator)

  const [activeTab, setActiveTab] = useState('Collection')
  const [loading, setLoading] = useState(true)

  //const [creator, setCreator] = useState(selectedCreator)

  const [artworks, setArtworks] = useState({
    collection: [],
    creation: [],
  })

 
  const creatorStatus = useSelector((state) => state.creator.status)

  useEffect(() => {
    ;(async () => {
      if (!creator && creatorStatus === 'succeeded') {
        let fetchedCreator = await creatorAPI.fetchUser({
          username: creatorUsername,
        })
        if(!fetchedCreator){
          history.push('/')
          return
        }
        dispatch(updateCreators(fetchedCreator))

      //  fetchedCreator ? setCreator(fetchedCreator) : history.push('/')
      }
    })()
  }, [creator, creatorStatus])

  useEffect(() => {
    ;(async () => {
      if (creator) {
        try {
          let fetchedCollection = await artworkAPI.fetcUserCollections(
            creator.publicAddress,
          )
          let fetchedCreation = await artworkAPI.fetchUserCreations(
            creator.username ? creator.username : creator.publicAddress,
          )
          setArtworks({
            collection: fetchedCollection,
            creation: fetchedCreation,
          })
          // if no colelction the active tab does to Creation
          if(fetchedCollection.length <= 0){
            setActiveTab('creation')
          }
          setLoading(false)
        } catch (err) {
          console.log(err)
          //set errro
        }
      }
    })()
  }, [creator])

  const handleTab = (e) => {
    e.preventDefault()
    let tab = e.target.dataset.value
    setActiveTab(tab)
  }

  const Tab = ({ name }) => {
    return (
      <li >
        <span 
          onClick={handleTab} 
          data-value={name} 
          className={activeTab === name ? 'item active' : 'item'}
          style={{textTransform: 'capitalize'}}> {name}</span>
      </li>

      // <div className={activeTab === name ? 'item active' : 'item'}>
      //   <div onClick={handleTab} data-value={name} className="text">
      //     {name}
      //   </div>
      // </div>
    )
  }
  

  const renderTabs = () => {
    if (!loading) {
      let collection = artworks.collection
      let creation = artworks.creation

      if(creation.length <= 0 && collection.length <= 0){
        return(
          <div className="center-container">
          <div className="big-text">Your collection is empty.</div>
          <div>Start building your collection by placing bids on artwork.</div>
          </div>
        )
      }

      return (
        <ul className="list-select-item clearfix">
            {collection.length > 0 ? <Tab name="collection" /> : <div></div>}
            {creation.length > 0 ? <Tab name="creation" /> : <div></div>}

        </ul>
       
      )
      
      {/* <div className="tabs-container">
          <div className="tabs">
            {collection.length > 0 ? <Tab name="Collection" /> : <div></div>}
            {creation.length > 0 ? <Tab name="Creation" /> : <div></div>}
          </div>
        </div> */}

    }
  }

  const renderArworks = () => {
    let collection = artworks.collection
    let creation = artworks.creation

    console.log(collection)

    if (activeTab === 'collection') {
      return collection.map((artwork) => {
        return (
          <div key={artwork._id} className="col-xl-4 col-lg-6 col-md-6 col-sm-6 col-12">
          <ArtworkCard2 artwork={artwork}/>
          </div>
      )      })
    } else if (activeTab === 'creation') {
      return creation.map((artwork) => {
        return (
          <div key={artwork._id} className="col-xl-4 col-lg-6 col-md-6 col-sm-6 col-12">
          <ArtworkCard2 artwork={artwork}/>
          </div>
      )      })
    }

  }



  if (!creator) {
    return <Loading />
  }

  let { coverImg, avatar, publicAddress, username, fullname, email } = creator
  coverImg = coverImg ? IMG_BASE + coverImg : CoverImg
  avatar = avatar ? IMG_BASE + avatar : AvatarImg
  fullname = fullname ? fullname : ''
  publicAddress = publicAddress ? publicAddress : ''

  return (
    <div className="root creator-page">
      <div className="header-container">
        <div className="cover-container">
          {isVideo(coverImg) ? (
            <Clip className="video" url={coverImg} />
          ) : (
            <img className="img" src={coverImg} />
          )}
        </div>
        <div className="avatar-container">
          {isVideo(avatar) ? (
            <Clip url={avatar} />
          ) : (
            <img className="avatar-img" src={avatar} />
          )}
        </div>
      </div>

      <div className="creator-content">
        <div className="row">
          <div className="col-md-3">
            <Sidebar creator={creator}/>
          </div>
          <div className="col-md-9">
          <div className="container-fluid creator-artworks ">
            {renderTabs()}
            <div className="browse-product-box">
              <div className="tab-content">
                <div className="row">

                {renderArworks()}

                </div>
              </div>
            </div>
           

          </div>
          </div>
       
        </div>
        
    
      </div>
    </div>
  )
}
export default Creator
