import { categories } from '../../constant'

const Sidebar = () => {
  return (
    <div className="browse-product-filter">
      <div className="filter-box">
        <h3 className="theme-title">Categories</h3>
        <div className="filter-menu">
          <ul>
            {categories && categories.map((category) => {
               return( <li key={"key-"+category}>
                  <input
                    className="styled-checkbox"
                    id={`checkbox-${category}`}
                    type="checkbox"
                    value={category}
                  />
                  <label htmlFor={`checkbox-${category}`}>
                    <span className="my-checkbox-label" >{category}</span>
                  </label>
                </li>)
            })}

          </ul>
        </div>
      </div>

      <div className="filter-box">
        <h3 className="theme-title">Sort by</h3>
        <div className="filter-menu">
          <ul>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-10"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-10">
                <span  className="my-checkbox-label">Newest</span>
              </label>
            </li>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-11"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-11">
                <span  className="my-checkbox-label">Oldest</span>
              </label>
            </li>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-12"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-12">
                <span  className="my-checkbox-label">Price - Low to high</span>
              </label>
            </li>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-13"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-13">
                <span className="my-checkbox-label">Price - High to low</span>
              </label>
            </li>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-14"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-14">
                <span className="my-checkbox-label">Auctions only</span>
              </label>
            </li>
          </ul>
        </div>
      </div>
      <div className="filter-box">
        <h3 className="theme-title">Price</h3>
        <div className="filter-menu">
          <ul>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-15"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-15">
                <span className="my-checkbox-label">$1 - $5.99</span>
              </label>
            </li>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-16"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-16">
                <span className="my-checkbox-label">$6 - $14.99</span>
              </label>
            </li>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-17"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-17">
                <span className="my-checkbox-label">$15 - $29.99</span>
              </label>
            </li>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-18"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-18">
                <span className="my-checkbox-label">$30 - $74.99</span>
              </label>
            </li>
            <li>
              <input
                className="styled-checkbox"
                id="styled-checkbox-19"
                type="checkbox"
                value="value1"
              />
              <label htmlFor="styled-checkbox-19">
                <span className="my-checkbox-label">$75 - $100+</span>
              </label>
            </li>
          </ul>
        </div>
      </div>
    </div>
  )
}

export default Sidebar
