import React, {useState} from 'react'
import {Link } from 'react-router-dom'
import Logo from '../../images/logo/icon/logowiithname.png'


const Footer = () => {

  return (
    <footer style={{marginTop: '50px'}} className="footer-main">
    <div className="footer-top">
       <div className="container">
          <div className="row">
             <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <Link to="/"><img src={Logo} alt=""/></Link>
                   <p className="theme-description">We are one of the best pre-designed template providers, for all the niches with more than 800 designs. </p>
                   <div className="footer-links clearfix">
                      <div className="footer-icon-box">
                         <a href="" title="Facebook" style={{color: '#3b5998'}}>
                         <i className="fab fa-facebook-f"></i>
                         </a>
                      </div>
                      <div className="footer-icon-box">
                         <a href="" title="Twitter" style={{color: '#00acee'}}>
                         <i className="fab fa-twitter"></i>
                         </a> 
                      </div>
                      <div className="footer-icon-box">
                         <a href="" title="Linkedin" style={{color: '#0e76a8'}}>
                         <i className="fab fa-linkedin-in"></i>
                         </a>
                      </div>
                   </div>
                </div>
             </div>
             <div className="col-xl-2 col-lg-2 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <h3 className="theme-title">My account</h3>
                   <div className="footer-menu-services">
                      <ul className="menu-service-menu">
                         <li><Link to="/creator/create">Create a Store</Link></li>
                         <li><Link to="/">List an Item for sale</Link></li>
                         <li><Link to="/">My Profile</Link></li>
                      </ul>
                   </div>
                </div>
             </div>
             <div className="col-xl-2 col-lg-2 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <h3 className="theme-title">Need Help?</h3>
                   <div className="footer-menu-services">
                      <ul className="menu-service-menu">
                         <li><Link to="/contactus">Help and support</Link></li>
                         <li><a target="_blank" href="/faq">FAQ</a></li>
                         <li><Link to="/contactus">Contact us</Link></li>
                      </ul>
                   </div>
                </div>
             </div>
             <div className="col-xl-2 col-lg-2 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <h3 className="theme-title">Buy an Item</h3>
                   <div className="footer-menu-services">
                      <ul className="menu-service-menu">
                         <li><Link to="/browse">Browse Digital Items</Link></li>
                         <li><Link to="/browse">Browse Stores</Link></li>
                         <li><Link to="/browse">Where to buy NFTs</Link></li>
                      </ul>
                   </div>
                </div>
             </div>
             <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
                <div className="footer-widget">
                   <h3 className="theme-title">Nft MarketPlace</h3>
                   <div className="footer-menu-services">
                      <ul className="menu-service-menu">
                         <li><a target="_blank" href="/privacy-policy">Privacy Policy</a></li>
                         <li><a target="_blank" href="/terms-of-use">Terms of use</a></li>
                      </ul>
                   </div>
                </div>
             </div>
          </div>
       </div>
    </div>
    <div className="footer-copyright">
       <div className="container">
          <div className="row">
             <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                <div className="copyright-text text-center">
                   <p>Copyright 2021 <a href="/">Digiplaza</a> All Rights Reserved.</p>
                </div>
             </div>
          </div>
       </div>
    </div>
 </footer>
  )
}
export default Footer
