import React, {useState} from 'react'
import {useSelector} from 'react-redux'
import {Link} from 'react-router-dom'
import Sidebar from './Sidebar'
import Bradcrumb from '../../components/Bradcrumb'
import ArtworkCard2 from '../../components/Card/ArtworkCard2'
import ArtworkList from '../../components/Card/ArtworkList'

const ViewNavItems = [
    {title: "card", icon:"fas fa-th-large" },
    {title: "list", icon:"fas fa-th-list" },
    // {title: "store", icon:"fas fa-list" },
]

const Browse = () => {

    const artworks = useSelector((state)=> state.artwork.artworks)
  //  console.log(selectedArtworks)
    // card, list, store
    const [viewTab, setViewTab] = useState("card")
  //  const [artworks, setArtworks] = useState(selectedArtworks)

    const ViewNavItem = ({item})=> {
        return(
            <li className="nav-item">
            <a
              className={`browse-list nav-link ${item.title == viewTab? 'active': ''}`}
              data-toggle="tooltip"
              data-placement="top"
              title={item.title}
              onClick={()=> setViewTab(item.title)}
            >
              <i className={item.icon}></i>
            </a>
          </li>
        )
    }

    const CardsView = ({artworks}) =>{
        return artworks.map((artwork)=>{
            return (
                <div key={artwork._id} className="col-xl-4 col-lg-6 col-md-6 col-sm-6 col-12">
                <ArtworkCard2 artwork={artwork}/>
                </div>
            )
        })
    }
    const ListView = ({artworks}) =>{
        return artworks.map((artwork)=>{
            return (
                <div key={artwork._id} className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                <ArtworkList artwork={artwork}/>
                </div>
            )
        })
    }

    const ProductsBox = ({artworks})=> {
        if(viewTab === "card"){
            return <CardsView artworks={artworks} />
        }else if(viewTab === "list"){
            return <ListView artworks={artworks} />
        }else{
            return (<><h1>No products</h1></>)
        }
        
    }


 return(
    <>
  <Bradcrumb/>

    <section
      className="browse-product-area page-paddings"
      style={{paddingTop: "70px"}}
    >
      <div className="container">
        <div className="row">
          <div className="col-xl-3 col-lg-4 col-md-4 col-sm-12 col-12">
            <Sidebar />
          </div>

          <div className="col-xl-9 col-lg-8 col-md-8 col-sm-12 col-12">
            <div className="browse-product-top">
              <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                  <div className="browse-product-left">
                    <p>Showing 25/50 Items</p>
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                  <div className="browse-product-right clearfix">
                    <ul className="nav nav-tabs" role="tablist">
                    {ViewNavItems && ViewNavItems.map((item)=>{
                        return <ViewNavItem key={item.title} item={item}/>
                    })}
                    </ul>
                  </div>
                </div>
              </div>
            </div>

            <div className="browse-product-box">
              <div className="tab-content">
                <div className="row">

                    <ProductsBox artworks={artworks}/>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </>
 )
}

export default Browse
