import React from 'react'
import { Link } from 'react-router-dom'


const ItemCard = (props) => {

    let {img, title, sold}=  props.category

  return (
    <div className="item-group">
    <div className="item-group-content">
       <div className="item-group-avtar">
          <img src={img} alt=""/>
       </div>
       <div className="selling-item-info">
          <h3 className="theme-title"><Link to={`/artworks?category=${title}`}>{title}</Link></h3>
          <p className="theme-description">Items sold: <span>{sold}</span></p>
       </div>
    </div>
 </div>
  )
}

export default ItemCard
