import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import PrivateRoute from './components/Route/PrivateRoute'

import Home from './pages/Home'
import Browse from './pages/Browse'
 import Creators from './pages/Creators'
// import Artworks from './pages/Artworks'
import Creator from './pages/Creator'
import Artwork from './pages/Artwork'
// import Profile from './pages/Profile'
import EditProfile from './pages/Profile/EditProfile'
import Mint from './pages/Mint'
import List from './pages/Mint/List'
//import Bid from './pages/Bid'
import Bids from './pages/Bids'
 import FAQ from './pages/Docs/faq'
 import TermsOfUse from './pages/Docs/termsofuse'
 import PrivacyPolicy from './pages/Docs/privacypolicy'
 import ContactUs from './pages/ContactUs'

import Header from './components/Header'
import Footer from './components/Footer'
import Loading from './components/Loading'

import { setMobileView } from './store/layout/layoutSlice'
import { fetchUser, setAccessToken } from './store/user/userSlice'
import { fetchAllCreators } from './store/creator/creatorSlice'
import { fetchAllArtworks } from './store/artwork/artworkSlice'
import { connectMetaMask } from './services/Web3/web3'

import './App.css'
import { LS_KEY } from './constant'

function App() {
  const dispatch = useDispatch()

  const userSlice = useSelector((state) => state.user)
  const creatorSlice = useSelector((state) => state.creator)
  const artworkSlice = useSelector((state) => state.artwork)

  const accessToken = userSlice.accessToken
  const userStatus = userSlice.status
  //const error = userSlice.error\

  useEffect(() => {
    // MOBILE
    const setResponsiveness = () => {
      return window.innerWidth < 900
        ? dispatch(setMobileView(true))
        : dispatch(setMobileView(false))
    }

    setResponsiveness()

    window.addEventListener('resize', () => setResponsiveness())
  }, [])

  useEffect(() => {
    try {
      let localAccessToken = window.localStorage.getItem(LS_KEY)
      if (localAccessToken && localAccessToken !== undefined) {
        localAccessToken = JSON.parse(localAccessToken)
        dispatch(setAccessToken(localAccessToken))
      }
    } catch (err) {
      console.log(err)
      window.localStorage.removeItem(LS_KEY)
    }
  }, [])

  useEffect(() => {
    ;(async () => {
      if (accessToken && userStatus === 'idle' && window.ethereum) {
        try {
          await connectMetaMask()
          dispatch(fetchUser(accessToken))
        } catch (err) {
          console.log(err)
          window.localStorage.removeItem(LS_KEY)
        }
      }
    })()
  }, [userStatus, accessToken, dispatch])

  useEffect(() => {
    if (artworkSlice.status === 'idle') {
      dispatch(fetchAllArtworks())
    }

    if (creatorSlice.status === 'idle') {
      dispatch(fetchAllCreators())
    }
  }, [])

  // if(
  //   artworkSlice.status !== 'succeeded'
  //   || creatorSlice.status !== 'succeeded'
  //   || userSlice.status !== 'succeeded'
  //   ){
  //   return <Loading/>
  // }

  return (
    <div className="App dark-theme">
      <Router>
        <Header />

        <Switch>
          <Route exact path="/" component={Home}></Route>
          <Route exact path="/browse" component={Browse}></Route>
          <Route exact path="/creators" component={Creators} />

          <Route exact path="/faq" component={FAQ} />

          <Route exact path="/contactus" component={ContactUs} />
          <Route exact path="/terms-of-use" component={TermsOfUse} />
          <Route exact path="/privacy-policy" component={PrivacyPolicy} />

          {/* <Route exact path="/artworks" component={Artworks} />
          <Route exact path="/creators" component={Creators} />

          <Route exact path="/profile" component={Profile} />
          <PrivateRoute exact path="/profile/edit" component={EditProfile} />

        
          <PrivateRoute exact path="/creator/list/:tokenId" component={List} />
          <PrivateRoute exact path="/bids" component={Bids} />
          
          <Route exact path="/faq" component={FAQ} />

          <Route exact path="/:creator" component={Creator} />
         
           */}
          <PrivateRoute exact path="/profile/edit" component={EditProfile} />
          <PrivateRoute exact path="/creator/create" component={Mint} />
          <PrivateRoute exact path="/creator/list/:tokenId" component={List} />
          <PrivateRoute exact path="/bids" component={Bids} />

          <Route exact path="/:creator" component={Creator} />
          {/* <Route exact path="/:creator/:artwork/bid" component={Bid} /> */}
          <Route exact path="/:creator/:artwork" component={Artwork} />
        </Switch>
        <Footer />
      </Router>
    </div>
  )
}

export default App
