import React, { useState, useEffect } from 'react'


const Countdown = ({ endTime, type }) => {
  endTime = isNaN(parseInt(endTime))? 0 : parseInt(endTime)
  const [countDown, setCountDown] = useState({
    time: {
      hours: 0,
      minutes: 0,
      seconds: 0,
    },
    endTime:  endTime,
  })

  const msToTime = (duration) => {
    let seconds = Math.floor(duration % 60)
    let minutes = Math.floor((duration / 60) % 60)
    let hours = Math.floor((duration / (60 * 60)) % 24)

    hours = hours.toString().padStart(2, '0')
    minutes = minutes.toString().padStart(2, '0')
    seconds = seconds.toString().padStart(2, '0')

    return {
      hours,
      minutes,
      seconds,
    }
  }

  let timerId = null

  useEffect(() => {
    if (!timerId) {
      const timer = setInterval(run, 1000)
      setCountDown({ ...countDown, timer: timer })
      timerId = timer
    }

    return () => {
      clearInterval(timerId)
    }
  }, [])

  const run = () => {
    let currentTime = parseInt(Math.floor(Date.now() / 1000))
    const diff = countDown.endTime - currentTime
    let remaining = diff
    if (remaining < 0) {
      remaining = 0
    }
    if (remaining === 0) {
      clearInterval(timerId)
    }

    let remaingTime = msToTime(remaining)

    setCountDown({
      ...countDown,
      time: {
        hours: remaingTime.hours,
        minutes: remaingTime.minutes,
        seconds: remaingTime.seconds,
      },
    })
  }

  // console.log("===timerId=====")
  // console.log(timerId)
  // if(!timerId){
  //   return(
  //     <div className={type}>
  //         <div className="big-text">Auction has ended</div>
  //       </div>
  //   )
  // }

  if (type == 'main-countdown') {
    return (
      <div className={type}>
        <div className="item">
          <span className="item-bold">{countDown.time.hours}</span>
          <span className="item-fade">Hours</span>
        </div>
        <div className="item">
          <span className="item-bold">{countDown.time.minutes}</span>
          <span className="item-fade">Minutes</span>
        </div>
        <div className="item">
          <span className="item-bold">{countDown.time.seconds}</span>
          <span className="item-fade">Seconds</span>
        </div>
      </div>
    )
  }

  if (type == 'artwork-card-2') {

    return (
      <ul className="clearfix">
        <li>
          <span>{countDown.time.hours}</span> Hour
        </li>
        <li>
          <span>{countDown.time.minutes}</span> Min
        </li>
        <li>
          <span>{countDown.time.seconds}</span> Sec
        </li>
      </ul>
    )
  }

  return (
    <div className={type}>
      <div className="item">
        <span className="">
          {countDown.time.hours}
          <span>h</span>
        </span>
        {/* <span className="">Hours</span> */}
      </div>
      <div className="item">
        <span className="">
          {countDown.time.minutes}
          <span>m</span>
        </span>
        {/* <span className="">Minutes</span> */}
      </div>
      <div className="item">
        <span className="">
          {countDown.time.seconds}
          <span>s</span>
        </span>
        {/* <span className="">Seconds</span> */}
      </div>
    </div>
  )
}

export default Countdown
