import React, { useState } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Clip from '../../components/Clip'
import Loading from '../../components/Loading'
import { isVideo } from '../../utils'
import ipfs from '../../services/ipfs'
import { patchUser } from '../../store/user/userSlice'
import { IMG_BASE } from '../../constant'

const EditProfile = (props) => {
  const dispatch = useDispatch()
  const history = useHistory()

  const user = useSelector((state) => state.user.user)
  const userState = useSelector((state) => state.user)

  if (userState.status === 'failed') {
    console.log(userState.error)
  }

  let { username, email, fullname, bio, avatar, coverImg } = user

  const [coverImage, setCoverImage] = useState({
    url: coverImg ? IMG_BASE + coverImg : '',
    data: '',
    type: isVideo(coverImg) ? 'video' : 'image',
  })
  const [avatarImg, setAvatarImg] = useState({
    url: avatar ? IMG_BASE + avatar : 'https://cencup.com/wp-content/uploads/2019/07/avatar-placeholder.png',
    data: '',
    type: isVideo(avatar) ? 'video' : 'image',
  })

  const [formData, setFormData] = useState({
    username: username ? username : '',
    email: email ? email : '',
    fullname: fullname ? fullname : '',
    bio: bio ? bio : '',
    avatar: avatar ? avatar : '',
    coverImg: coverImg ? coverImg : '',
    instagram: user.instagram ? user.instagram : '',
    youtube: user.youtube ? user.youtube : '',
    tiktok: user.tiktok ? user.tiktok : '',
    facebook: user.facebook ? user.facebook : '',
    button:false
  })

  // if (!user) {
  //   return <Loading />
  // }

  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
  }

  const handleOnSubmit = async (e) => {
    e.preventDefault()
    if (userState.status === 'pending') {
      return
    }
    console.log('clickred')
    setFormData({...formData, button: true})
    if(formData.button){
      return
    }
    try {
      let avatarPath = '/nft.jpg'
      let coverImgPath = '/nft.jpg'
      let avatarHash = avatar
      let coverImgHash = coverImg

      if (avatarImg.type === 'video') {
        avatarPath = '/nft.mp4'
      }
      if (coverImage.type === 'video') {
        coverImgPath = '/nft.mp4'
      }
      //only add if its there
      let ipfsAvatarResponse = null
      let ipfsCoverImgResponse = null

      if (avatarImg.data !== '') {
        ipfsAvatarResponse = await ipfs.add(
          {
            path: avatarPath,
            content: avatarImg.data,
          },
          { wrapWithDirectory: true },
        )

        avatarHash = ipfsAvatarResponse[1].hash
        avatarHash = avatarHash + avatarPath
      }

      if (coverImage.data !== '') {
        ipfsCoverImgResponse = await ipfs.add(
          {
            path: coverImgPath,
            content: coverImage.data,
          },
          { wrapWithDirectory: true },
        )

        coverImgHash = ipfsCoverImgResponse[1].hash
        coverImgHash = coverImgHash + coverImgPath
      }

      let formdata = {
        ...formData,
        avatar: avatarHash,
        coverImg: coverImgHash,
      }
      console.log("dispatching")
      dispatch(patchUser(formdata))
      setFormData({...formData, button: false})

    } catch (err) {
      setFormData({...formData, button: false})

      console.log(err)
    }
  }

  const handleImgUpload = async (e) => {
    e.stopPropagation()
    e.preventDefault()
    const file = e.target.files[0]
    const name = e.target.files[0].name
    let fileType = 'image'
    let type = e.target.name

    let imgUrl = await readImg(file, 'read')
    let imgData = await readImg(file, 'data')

    const imgBuffer = await Buffer.from(imgData)

    if (isVideo(name)) {
      fileType = 'video'
    }

    if (type === 'coverImg') {
      setCoverImage({
        ...coverImg,
        url: imgUrl,
        data: imgBuffer,
        type: fileType,
      })
    } else {
      setAvatarImg({
        ...avatarImg,
        url: imgUrl,
        data: imgBuffer,
        type: fileType,
      })
    }
  }

  const readImg = (file, type) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      if (type === 'data') {
        reader.readAsArrayBuffer(file)
      } else {
        reader.readAsDataURL(file)
      }
      reader.onloadend = async () => {
        resolve(reader.result)
      }
    })
  }

  return (
    <section style={{ marginTop: '100px' }} className="user-panel-main-box">
      <div className="container">
        <div className="row">
          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
            <div className="user-panel-main">
              <div className="user-panel-breadcrumb">
                <div className="row">
                  <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                    <div className="user-panel-breadcrumb-left">
                      <h1>My Profile</h1>
                      <div className="user-link">
                        <a href=""></a>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                    <div className="user-panel-breadcrumb-right clearfix">
                      <ul className="clearfix">
                        <li>
                          <div className="user-setting-box">
                            <Link to="/creator/create">
                              <i className="fas fa-plus"></i>
                              <h4>Create item</h4>
                            </Link>
                          </div>
                        </li>
                        {/* <li>
                          <div className="user-setting-box">
                            <a href="">
                              <i className="fas fa-cog"></i>
                              <h4>Settings</h4>
                            </a>
                          </div>
                        </li> */}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>

              <div className="user-store-area">
                <div className="row">
                  <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                    <div className="user-setting-box-area">
                      <div className="row user-setting-box-top">
                        <div className="col-xl-3 col-lg-3 col-md-3 col-sm-12 col-12 user-setting-left">
                          <div className="setting-change-profile text-center">
                            <div className="change-profile-icon">
                             <label htmlFor="avatarImg">
                             {avatarImg.type === 'video' ? (
                                <Clip className="avatar-img" url={avatarImg.url} />
                              ) : (
                                <img className="avatar-img" src={avatarImg.url} />
                              )}
                             </label>
                            </div>
                            <input style={{display:'none'}} id="avatarImg" name="avatar" type="file" onChange={handleImgUpload} />
                            <h3 className="user-title">Change Image</h3>
                          </div>
                        </div>
                        <div className="col-xl-9 col-lg-9 col-md-9 col-sm-12 col-12 user-setting-right">
                          {/* {dailog.active ? (
                            <div className="alert alert-primary" role="alert">
                              <div className={`alert alert-${
                                  dailog.status === 'error'
                                    ? 'danger'
                                    : 'success'
                                }`}
                              >{dailog.message}
                              </div>
                            </div>
                          ) : (<></>)} */}

                          <div className="row">
                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                              <div className="theme-input-box">
                                <label>Username</label>
                                <input
                                  className="theme-input"
                                  name="username"
                                  placeholder="Username"
                                  required
                                  value={formData.username}
                                  onChange={handleChange}
                                />
                              </div>
                            </div>
                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                              <div className="theme-input-box">
                                <label>Full Name</label>
                                <input
                                  className="theme-input"
                                  name="fullname"
                                  placeholder="Full Name"
                                  required
                                  value={formData.fullname}
                                  onChange={handleChange}
                                />
                              </div>
                            </div>
                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                              <div className="theme-input-box">
                                <label>Email</label>
                                <input
                                  className="theme-input"
                                  type="email"
                                  name="email"
                                  placeholder="Email"
                                  required
                                  value={formData.email}
                                  onChange={handleChange}
                                />
                              </div>
                            </div>
                            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                              <div className="theme-input-box">
                                <p className="theme-description">Bio</p>
                                <textarea
                                  className="theme-input"
                                  rows="4"
                                  name="bio"
                                  placeholder="bio"
                                  required
                                  value={formData.bio}
                                  onChange={handleChange}
                                ></textarea>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="row">
                        <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                          <div className="theme-form-title text-center">
                            <h3 className="theme-title">
                              <span>Social Media</span>
                            </h3>
                          </div>
                        </div>
                      </div>
                      <div className="row">
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                          <div className="theme-input-box">
                            <label>Facebook</label>
                            <input
                              className="theme-input"
                              name="facebook"
                              value={formData.facebook}
                              onChange={handleChange}
                              placeholder="link to your facebook"
                            />
                          </div>
                        </div>
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                          <div className="theme-input-box">
                            <label>Tiktok</label>
                            <input
                              className="theme-input"
                              name="tiktok"
                              value={formData.tiktok}
                              onChange={handleChange}
                              placeholder="link to your tiktok"
                            />
                          </div>
                        </div>
 
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                          <div className="theme-input-box">
                            <label>Youtube</label>
                            <input
                              className="theme-input"
                              name="youtube"
                              value={formData.youtube}
                              onChange={handleChange}
                              placeholder="link to your youtube"
                            />
                          </div>
                        </div>
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                          <div className="theme-input-box">
                            <label>Instagram</label>
                            <input
                              className="theme-input"
                              name="instagram"
                              value={formData.instagram}
                              onChange={handleChange}
                              placeholder="link to your instagram"
                            />
                          </div>
                        </div>
                      </div>

                      <div className="row">
                        <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                          <div
                            className="theme-input-box text-center"
                            style={{ margintop: '30px' }}
                          >
                            {formData.button? ( <button className="theme-btn transparent-btn" >  Upadting... </button>)
                            :( <button
                              onClick={handleOnSubmit}
                              className="theme-btn" >
                              Save Changes
                            </button>)}
                           
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default EditProfile
